import "./index.scss"; 

export default function Background() {

    return (
        <div className="background">
            <div className="background-container">
                <div className="background-column"></div>
                <div className="background-block-1 background-block fade-up"></div>
                <div className="background-block-2 background-block"></div>
            </div>
        </div>
    )
}